$phone: 640px;
$tablet: 768px;
$desktop: 1024px;
.navbar {
  // background-color: #3772a9;
}
.text-warning {
  font-family: GE;
  // color: #fffc35 !important;
}
.responsive-navbar-nav {
  direction: rtl;
}
.nav-item-container {
  display: flex;
  // width: 100%;
  justify-content: center;
  flex-direction: column;
}
.nav-item {
  display: flex;
  // width: 100%;
  justify-content: center;
}
.requests-not-found {
  display: flex;
  justify-content: center;
}
.header-item-title {
  display: none;
}
.form-inline {
  width: 250px !important;
  margin-left: 10px;
}
.search-box {
  border-radius: 5px !important;
}
.dropdown-menu {
  max-height: 323px;
  // max-height: 215px;
  overflow-y: auto;
}
.notifications-dropdown {
  display: flex;
  padding: 0 7px;
  align-items: center;
  min-width: 299px;

  .accept-and-reject-button {
    padding: 0;
    margin: 0px 6px;
    height: 25px;
  }
  .notifications-person-name {
    justify-content: space-between;
    display: flex;
    font-size: 22px;
    padding: 5px 10px;
    text-align: right;
    // &:only-of-type {
    //   border-radius: 0px;
    // }
    &:first-of-type {
      border-radius: 0 0 5px 5px;
    }
    &:last-of-type {
      border-radius: 5px 5px 0 0;
    }
  }
}
.dropdown-toggle::after {
  display: none !important;
}

@media (max-width: 990px) {
  .form-inline {
    width: auto !important;
    margin-left: 0;
  }
  .header-item-title {
    display: block;
  }
}
